* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.invisible {
  display: none;
}

#navbar-container {
  width: 100vw;
  height: 10vh;
  background-color: #f5f7fc;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  box-shadow: 0 5px 15px #00000059;
}

#navbar {
  width: 40%;
  justify-content: space-evenly;
  margin-right: 8px;
  list-style: none;
  display: flex;
}

#navbar li {
  width: 100px;
  height: 42px;
  justify-content: center;
  align-items: center;
  padding: 2%;
  display: flex;
}

#navbar li:nth-child(2) {
  background-color: #3ad952cc;
  border-radius: 10px;
}

#navbar li:nth-child(2) a {
  color: #fff;
}

#navbar li a {
  color: #383838;
  padding: 2%;
  font-size: .9rem;
  text-decoration: none;
}

#background-img-container {
  width: 100vw;
  height: 85vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#background-img-container img {
  width: 80%;
  border-radius: 12px;
  margin: 5%;
  box-shadow: 0 50px 100px -20px #32325d40, 0 30px 60px -30px #0000004d, inset 0 -2px 6px #0a254059;
}

#welcome-message-container {
  width: 80%;
  height: 100vh;
  border-radius: 12px;
  margin: 1%;
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px #0000000f;
}

#welcome-message-container p {
  text-align: justify;
  padding: 3%;
  font-size: 1rem;
}

.signup-form-container {
  width: 100vw;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.signup-form {
  max-width: 500px;
  background-color: #fff;
  border-radius: 8px;
  margin: 0 auto;
  padding: 2rem;
  box-shadow: 0 5px 15px #00000059;
}

.form-field {
  flex-direction: column;
  margin-bottom: 1.5rem;
  display: flex;
}

label {
  margin-bottom: .5rem;
  font-size: .9rem;
  font-weight: 500;
}

#signup-username, #signup-email, #signup-password {
  border: 1px solid #d4d4d4;
  border-radius: 4px;
  outline: none;
  padding: .7rem;
  font-size: .9rem;
  transition: border .3s;
}

#signup-username:focus, #signup-email:focus, #signup-password:focus {
  border-color: #4f7bf6;
}

.radio-buttons {
  flex-wrap: wrap;
  display: flex;
}

.radio-buttons label {
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  margin: .5rem;
  display: flex;
}

.radio-buttons img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
  transition: box-shadow .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.radio-buttons input[type="radio"]:checked + img {
  box-shadow: 0 4px 6px #0003;
}

#signup-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4f7bf6;
  border: none;
  border-radius: 4px;
  padding: .7rem;
  font-size: 1rem;
  font-weight: 500;
  transition: background-color .3s;
}

#signup-btn:hover {
  background-color: #3a5ad9;
}

.form-container {
  width: 100vw;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#login-form, #delete-account-page-form {
  width: 25vw;
  height: 75vh;
  border-radius: 8px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3%;
  display: flex;
  box-shadow: 0 5px 15px #00000059;
}

#login-email, #login-password, #delete-account-page-password, #delete-account-page-email {
  width: 80%;
  height: 10%;
  border: 1px solid #a4a4a4;
  border-radius: 4px;
  font-size: 20px;
}

#login-btn {
  width: 80%;
  color: #fff;
  cursor: pointer;
  background-color: #4f7bf6;
  border: none;
  border-radius: 4px;
  padding: .7rem;
  font-size: 1rem;
  font-weight: 500;
}

#delete-account-page-btn {
  width: 80%;
  color: #fff;
  cursor: pointer;
  background-color: #f64f4f;
  border: none;
  border-radius: 4px;
  padding: .7rem;
  font-size: 1rem;
  font-weight: 500;
}

#logged-header {
  width: 100vw;
  height: 10vh;
  background-color: #f5f7fc;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 5px 15px #00000059;
}

.logo {
  width: 10%;
}

#user-info-container {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

#current-user-img-container {
  width: 4%;
}

#current-user {
  margin: 0 3% 0 1%;
}

.current-user-img {
  width: 100%;
  border-radius: 50%;
}

#logged-header h1, #logged-header p {
  margin: 0 32px;
}

#logged-container {
  min-height: 90vh;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.my-updates {
  max-height: 85vh;
  border-radius: 8px;
  flex-direction: column;
  grid-area: 1 / 2 / 3 / 3;
  justify-content: space-around;
  align-items: center;
  margin: 3%;
  display: flex;
  box-shadow: 0 0 .25em #43475545, 0 .25em 1em #5a7dbc0d;
}

.my-updates h2 {
  margin-top: 5%;
}

#my-status-updates {
  width: 85%;
  height: 70%;
  border-radius: 8px;
  margin: 3% 0;
  overflow: auto;
  box-shadow: 0 0 0 2px #06182c66, 0 4px 6px -1px #06182ca6, inset 0 1px #ffffff14;
}

#my-status-updates li {
  height: 3.5rem;
  text-align: center;
  border-radius: 8px;
  margin: 2%;
  padding: 1%;
  list-style: none;
  box-shadow: 1.95px 1.95px 2.6px #00000026;
}

#new-status-update-form {
  width: 85%;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  padding: 3%;
  display: flex;
  box-shadow: 0 0 0 2px #06182c66, 0 4px 6px -1px #06182ca6, inset 0 1px #ffffff14;
}

#new-status-update {
  width: 60%;
  height: 100%;
  font-size: 20px;
  box-shadow: 0 1px 3px #00000005, 0 0 0 1px #1b1f2326;
}

#new-status-update-btn {
  width: 15%;
  color: #fff;
  cursor: pointer;
  background-color: #4f7bf6;
  border: none;
  border-radius: 4px;
  margin-left: .3rem;
  padding: .7rem;
  font-size: .8rem;
  font-weight: 500;
}

.other-users-container {
  max-height: 85vh;
  border-radius: 8px;
  flex-direction: column;
  grid-area: 1 / 1 / 3 / 2;
  justify-content: flex-start;
  align-items: center;
  margin: 3%;
  padding: 3%;
  display: flex;
  box-shadow: 0 0 .25em #43475545, 0 .25em 1em #5a7dbc0d;
}

#other-users {
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  overflow: auto;
}

#other-users h2 {
  margin: 2%;
}

.other-users-img {
  width: 10%;
  border-radius: 50%;
}

.other-user-btn {
  border-bottom: 1px solid silver;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 3%;
  display: flex;
}

.other-user-btn p {
  margin-left: 1%;
}

.delete-account {
  max-height: 85vh;
  flex-direction: column;
  grid-column: 3 / 4;
  align-items: flex-end;
  margin: 5%;
  display: flex;
}

#logout-form {
  width: 25%;
  height: 10%;
  justify-content: flex-end;
  margin: 8px 0;
  display: flex;
}

#logout-btn {
  width: 100%;
  color: #efefef;
  background-color: #3a3a3a;
  border-radius: 8px;
  font-size: 14px;
  box-shadow: 0 2px 8px #63636333;
}

#delete-account-form {
  width: 25%;
  height: 10%;
  justify-content: flex-end;
  margin: 8px 0;
  display: flex;
}

#delete-account-btn {
  width: 100%;
  color: #efefef;
  background-color: #ff4242;
  border-radius: 8px;
  font-size: 10px;
  box-shadow: 0 2px 8px #63636333;
}

.other-user-btn:hover {
  color: #fff;
  background-color: #61b5d4;
}

#visited-header {
  width: 100vw;
  height: 10vh;
  background-color: #f5f7fc;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 5px 15px #00000059;
}

#visited-header h1 {
  color: #202020;
  margin-right: 5%;
}

#other-user-profile-container {
  grid-template-rows: 1fr;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

#other-user-profile-info {
  flex-direction: column;
  grid-column: 1 / 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

#other-user-profile-posts {
  flex-direction: column;
  grid-column: 2 / 3;
  align-items: center;
  display: flex;
  overflow: auto;
}

#visited-user-img {
  width: 25%;
  border-radius: 10%;
  margin-top: 3%;
  margin-bottom: 2%;
  box-shadow: 0 3px 8px #0000003d;
}

#visited-user-name {
  font-size: 2rem;
}

#visited-user-contact {
  font-size: 1.05rem;
}

#visited-post-container {
  width: 80%;
  color: #000;
  border-radius: 10px;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3%;
  margin-bottom: 1%;
  padding: 2%;
  display: flex;
}

#visited-post-like-btn {
  width: 7%;
  background-color: #0000;
  border-radius: 10px;
  padding: 1%;
}

#visited-post-like-btn svg {
  stroke: #ff2828;
}

#navbar li:nth-child(2):hover {
  background-color: #00d620cc;
  border-radius: 10px;
  box-shadow: 0 1px 4px #00000029;
}

#visited-post-like-btn svg:hover {
  fill: #ff2828;
}

#logout-btn:hover {
  background-color: #0a0a0a;
  box-shadow: 0 3px 6px #00000029, 0 3px 6px #0000003b;
}

#delete-account-btn:hover, #delete-account-page-btn:hover {
  background-color: #ff2828;
  box-shadow: 0 3px 6px #00000029, 0 3px 6px #0000003b;
}

#new-status-update-btn:hover {
  background-color: #215bf9;
  box-shadow: 0 3px 6px #00000029, 0 3px 6px #0000003b;
}

#login-btn:hover {
  background-color: #3a5ad9;
}

@media only screen and (min-width: 480px) and (max-width: 768px) {
  #welcome-message-container p {
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  #background-img-container {
    flex-direction: row;
  }

  #welcome-message-container {
    height: 60vh;
    align-items: center;
    display: flex;
  }

  #welcome-message-container p {
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 1024px) {
  #navbar li {
    width: 100px;
